import { useState, useEffect } from 'react';
import { useGetAvailableSubscriptionsQuery } from '@/apollo/graphql';
import { ChargeBeeItemEnum, type PlanData, Plans } from '@/screens/Plan/types';
import { useLaunchDarkly, useError } from '@/hooks';
import {
  computeNoumRenewalPlanUsingPayAsYouGo,
  computeNoumSetupPlanUsingPayAsYouGo,
} from '@/screens/Plan/helper';

export const useSubscription = () => {
  const [subscribedPlans, setSubscribedPlans] = useState<
    (PlanData | undefined | null)[]
  >([]);
  const [payAsYouGoPlan, setPayAsYouGoPlan] = useState<
    (PlanData | undefined | null)[]
  >([]);

  const { flags } = useLaunchDarkly();

  const { logError } = useError();

  const { data, loading, error, refetch } = useGetAvailableSubscriptionsQuery({
    fetchPolicy: 'network-only',
    skip: !flags.paymentSubscriptions,
    variables: {},
    onError: () => {
      logError(error, 'GetAvailableSubscriptions');
    },
  });

  useEffect(() => {
    setSubscribedPlans(
      data?.getAvailableSubscriptions.filter(
        (x) => x.plan_type === ChargeBeeItemEnum.PLAN,
      ) ||
        [] ||
        [],
    );
    setPayAsYouGoPlan(
      data?.getAvailableSubscriptions.filter(
        (x) => x.plan_type === ChargeBeeItemEnum.CHARGE,
      ) ||
        [] ||
        [],
    );
  }, [data]);

  const activeSubscription = (subscribedPlans || []).filter(
    (plan) => plan?.status === Plans.ACTIVE,
  );

  const activeCancelledSubscription = (subscribedPlans || []).filter(
    (plan) => plan?.status === Plans.ACTIVE || plan?.status === Plans.CANCELLED,
  );

  const expiredSubscription = (subscribedPlans || []).filter(
    (plan) => plan?.status === Plans.INACTIVE || plan?.status === Plans.EXPIRED,
  );

  const noumRenewalSlotsPayAsYouGo = (payAsYouGoPlan || []).reduce(
    (acc, obj) =>
      acc + (obj?.max_count_noum_renewal ? obj.max_count_noum_renewal : 0),
    0,
  );
  const noumSetupSlotsPayAsYouGo = (payAsYouGoPlan || []).reduce(
    (acc, obj) =>
      acc + (obj?.max_count_noum_setup ? obj.max_count_noum_setup : 0),
    0,
  );

  const totalNoumRenewalSlots = (activeSubscription || []).reduce(
    (acc, obj) =>
      acc + (obj?.max_count_noum_renewal ? obj.max_count_noum_renewal : 0),
    0,
  );
  const totalNoumSetupSlots = (activeSubscription || []).reduce(
    (acc, obj) =>
      acc + (obj?.max_count_noum_setup ? obj.max_count_noum_setup : 0),
    0,
  );
  const activeNoumRenewalSlots = (activeSubscription || []).reduce(
    (acc, obj) =>
      acc +
      (obj?.active_count_noum_renewal ? obj.active_count_noum_renewal : 0),
    0,
  );
  const activeNoumSetupSlots = (activeSubscription || []).reduce(
    (acc, obj) =>
      acc + (obj?.active_count_noum_setup ? obj.active_count_noum_setup : 0),
    0,
  );

  const computeOldestPlanSetup = (dataset: (PlanData | null | undefined)[]) => {
    const resultArray: (PlanData | null | undefined)[] = [];
    for (let i = dataset.length - 1; i >= 0; i -= 1) {
      const obj = dataset[i];
      if (
        (obj?.max_count_noum_setup ? obj.max_count_noum_setup : 0) -
          (obj?.active_count_noum_setup ? obj.active_count_noum_setup : 0) >
        0
      ) {
        resultArray.push(obj);
      }
    }
    return resultArray;
  };
  const computeOldestPlanRenewal = (
    dataset: (PlanData | null | undefined)[],
  ) => {
    const resultArray: (PlanData | null | undefined)[] = [];
    for (let i = dataset.length - 1; i >= 0; i -= 1) {
      const obj = dataset[i];
      if (
        (obj?.max_count_noum_renewal ? obj.max_count_noum_renewal : 0) -
          (obj?.active_count_noum_renewal ? obj.active_count_noum_renewal : 0) >
        0
      ) {
        resultArray.push(obj);
      }
    }
    return resultArray;
  };

  const oldestPlanNoumSetup: (PlanData | null | undefined)[] =
    computeOldestPlanSetup(activeSubscription);
  const oldestPlanNoumRenewal: (PlanData | null | undefined)[] =
    computeOldestPlanRenewal(activeSubscription);

  const payAsYouGoNoumRenewal: (PlanData | null | undefined)[] =
    computeNoumRenewalPlanUsingPayAsYouGo(payAsYouGoPlan);

  const payAsYouGoNoumSetup: (PlanData | null | undefined)[] =
    computeNoumSetupPlanUsingPayAsYouGo(payAsYouGoPlan);

  useEffect(() => {
    refetch();
  }, [refetch]);

  return {
    subscribedPlans,
    payAsYouGoPlan,
    loading,
    activeSubscription,
    expiredSubscription,
    refreshSubscriptions: refetch,
    noumRenewalSlotsPayAsYouGo,
    noumSetupSlotsPayAsYouGo,
    totalNoumRenewalSlots,
    totalNoumSetupSlots,
    activeNoumRenewalSlots,
    activeNoumSetupSlots,
    oldestPlanNoumSetup,
    oldestPlanNoumRenewal,
    payAsYouGoNoumRenewal,
    payAsYouGoNoumSetup,
    activeCancelledSubscription,
  };
};
