import { useContext, useEffect, useState } from 'react';
import { t } from 'i18next';
import { useCreateChargebeeSubscriptionMutation } from '@/apollo/graphql';
import { useSubscription } from '@/features/money/hooks';
import { type SubscriptionOutput } from '@/apollo/generated/types';
import { ChargeBeeContext } from '@/providers';
import { useError, useToast } from '@/hooks';

interface TuseChargebeeRegisterAgain {
  success?: (subscriptionObject: SubscriptionOutput) => void;
  error?: Function;
  loaded?: Function;
  close?: () => void;
  step?: (step: string) => void;
}

type TChragbeeCustomer = {
  first_name: string;
  last_name: string;
  email: string;
};

type TuseChargebeeCheckout = {
  loading: boolean;
};

const useChargebeeCheckout = (
  checkoutCallbacks?: TuseChargebeeRegisterAgain,
): TuseChargebeeCheckout => {
  const { refreshSubscriptions } = useSubscription();
  const { customerDetails } = useContext(ChargeBeeContext);
  const { logError } = useError();
  const { addToast } = useToast();
  const [chargbeeUser, setChargbeeUser] = useState<TChragbeeCustomer>({
    first_name: '',
    last_name: '',
    email: '',
  });

  useEffect(() => {
    setChargbeeUser({
      first_name: customerDetails?.first_name || '',
      last_name: customerDetails?.last_name || '',
      email: customerDetails?.email || '',
    });
  }, [customerDetails]);

  const [createSubscription, { loading: createSubscriptionLoading }] =
    useCreateChargebeeSubscriptionMutation({
      onCompleted: (data) => {
        if (data) {
          if (checkoutCallbacks && checkoutCallbacks.success) {
            checkoutCallbacks.success(
              data.createSubscriptionAndInvoiceFromHostedPages,
            );
            refreshSubscriptions();
          }
          addToast(
            'success',
            'none',
            `${t('noumena.money.subscription.subscription.successful')}`,
          );
        }
      },
      onError: (error) => {
        logError(error, 'createSubscriptionAndInvoiceFromHostedPages');
      },
    });

  const loading = createSubscriptionLoading;

  useEffect(() => {
    window.Chargebee.registerAgain();
    const cbInstance = window.Chargebee.getInstance();
    if (cbInstance) {
      const cart = cbInstance?.getCart();
      cart?.setCustomer(chargbeeUser);
      if (checkoutCallbacks) {
        const { error, loaded, close, step } = checkoutCallbacks;
        cbInstance?.setCheckoutCallbacks(() => ({
          success: (hostedPageId: string) => {
            createSubscription({
              variables: {
                hosted_id: hostedPageId,
              },
            });
          },
          error,
          loaded,
          close,
          step,
        }));
      }
    }
    return () => {
      if (cbInstance) {
        cbInstance.cart?.setCustomer(undefined);
        cbInstance?.setCheckoutCallbacks(() => ({}));
      }
    };
  }, [
    checkoutCallbacks,
    chargbeeUser,
    refreshSubscriptions,
    createSubscription,
  ]);

  return { loading };
};

export default useChargebeeCheckout;
