import { useCallback } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { useToast } from '@/hooks/toast';
import { useAddRenewNoumTransactionFeeMutation } from '@/apollo/graphql';
import { type NoumTransactionInputType } from '@/apollo/generated/types';

export function useNoumRenewalHelper() {
  const { addToast } = useToast();

  const handleError = useCallback(
    (networkError: String | Error | null) => {
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${networkError}`,
      );
    },
    [addToast],
  );

  const handleSuccess = useCallback(() => {
    addToast(
      'success',
      'icon',
      `${t('noumena.toast_success.text')}: Noum Renewed`,
    );
  }, [addToast]);

  const [addRenewNoumMutation] = useAddRenewNoumTransactionFeeMutation();

  const noumRenewalHelper = useCallback(
    async (input: NoumTransactionInputType) => {
      let isSuccess;
      await addRenewNoumMutation({
        variables: { noumInput: input },
        onError: ({ networkError = null, graphQLErrors = [] }) => {
          const [err] = graphQLErrors;
          handleError(err?.message ?? networkError);
          Sentry.captureException(new Error(err?.message ?? networkError), {
            tags: {
              section: 'addRenewNoumMutation',
            },
          });
          isSuccess = false;
        },
        onCompleted: (res) => {
          if (res.addRenewNoumTransactionFee.chamber_id) {
            handleSuccess();
            isSuccess = true;
          } else {
            isSuccess = false;
          }
        },
      });
      return isSuccess;
    },
    [handleError, handleSuccess, addRenewNoumMutation],
  );

  return { noumRenewalHelper };
}

export default useNoumRenewalHelper;
