    `}
	${({ spotlighted }) =>
    spotlighted &&
    css`
      background-color: var(--bg-card-brand-secondary-highlighted);
      border-bottom: 1px solid var(--border-card-brand-primary-default);
      border-top: 1px solid var(--border-card-brand-primary-default);
    `}
`;
