/* eslint-disable react/no-array-index-key */
import { memo } from 'react';
import { useParams } from 'react-router';
import { t } from 'i18next';
import { Trans } from 'react-i18next';
import { TSpan } from '@/components/Typography';
import { Button } from '@/components/Button';
import { useDeviceType, DeviceTypeEnum } from '@/hooks/devices';
import {
  useSubscription,
  useChargebeeCheckout,
  useNoumRenewalHelper,
  useNoumTransactions,
} from '@/features/money/hooks';
import {
  AllCurrencyEnum,
  type NoumTransactionDetailInputType,
  Noum_Fee_Operation_Type,
  Subscription_Plan_Type,
} from '@/apollo/generated/types';
import convertToCurrency from '@/utils/currencyToCurrency';
import { Stack } from '@/layout';
import Tooltip from '@/components/Tooltip';
import { PurschaseModalLaunchFronEnum, SCREEN } from '../types';
import IndivisualPlanDetails from './IndivisualPlanDetails';
import SubscriptionCardSkeleton from './SubscriptionCardSkeleton';
import { EToggle, type TableViewProps } from './types';
import * as Component from './styles';

const TableView = memo(
  ({
    plans,
    toggleButtonState,
    loading,
    launchFrom,
    itemType,
    launchScreen,
    onClosePurchasePlanModal,
    chamberIdAfterCreatingNoum,
    publishAndSubscribeNoum,
    chamber_id,
    noum_transaction_fee_id,
    onPlanPurchase,
  }: TableViewProps) => {
    const { noumRenewalHelper } = useNoumRenewalHelper();
    const { refreshSubscriptions } = useSubscription();
    const device = useDeviceType();

    const isNotLaptop =
      device === DeviceTypeEnum.TABLET || device === DeviceTypeEnum.MOBILE;

    const { id } = useParams();

    const requestParameter = () => {
      let parameter: NoumTransactionDetailInputType = {};
      if (launchScreen === SCREEN.PLAN_DETAILS) {
        if (itemType === Subscription_Plan_Type.Charge) {
          parameter = { plan_type: Subscription_Plan_Type.Charge };
        } else {
          parameter = { subscription_id: Number(id) };
        }
      }

      return parameter;
    };

    const { refreshNoumData } = useNoumTransactions(requestParameter());
    const { loading: chragebeeLoading } = useChargebeeCheckout({
      success: async (subscriptionOBj) => {
        onClosePurchasePlanModal();
        if (launchFrom === PurschaseModalLaunchFronEnum.NOUM_SETUP) {
          if (
            chamberIdAfterCreatingNoum &&
            subscriptionOBj.subscription_id &&
            publishAndSubscribeNoum
          ) {
            await publishAndSubscribeNoum(subscriptionOBj.subscription_id);
          }
        }
        if (launchFrom === PurschaseModalLaunchFronEnum.INITIAL_SETUP) {
          refreshSubscriptions();
        }
        if (
          launchFrom === PurschaseModalLaunchFronEnum.NOUM_RENEWAL &&
          chamber_id &&
          noum_transaction_fee_id
        ) {
          const isSuccess = await noumRenewalHelper({
            chamber_id,
            operation_type: Noum_Fee_Operation_Type.Renewal,
            subscription_id: subscriptionOBj?.subscription_id!,
            noum_transaction_fee_id,
          });

          if (isSuccess) {
            refreshSubscriptions();
            refreshNoumData();
          }
        }
      },
    });

    return (
      <>
        {loading && <SubscriptionCardSkeleton />}
        {!loading && (
          <Component.Table>
            <Component.Thead>
              <Component.Tr>
                <Component.Th width="210" hideLeftBorder />
                <Component.Th>
                  <Stack fullWidth justify="center">
                    <TSpan
                      font="footnote-bold"
                      colorToken="--text-card-neutral-default"
                    >
                      Transaction Fee
                    </TSpan>
                  </Stack>
                  <Tooltip top={40} left={65}>
                    <div style={{ width: '284px', textAlign: 'left' }}>
                      <TSpan
                        colorToken="--text-tooltip-neutral-alt-default"
                        font="footnote"
                        textAlign="left"
                      >
                        {t(
                          'noumena.money.subscription.noum.transactionfee.tooltip.text',
                        )}
                      </TSpan>
                    </div>
                  </Tooltip>
                </Component.Th>
                <Component.Th center>
                  <TSpan
                    font="footnote-bold"
                    colorToken="--text-card-neutral-default"
                  >
                    Noum <br /> Set-up Fee
                  </TSpan>
                </Component.Th>
                <Component.Th center>
                  <TSpan
                    font="footnote-bold"
                    colorToken="--text-card-neutral-default"
                  >
                    Noum <br />
                    Renewal Fee
                  </TSpan>
                </Component.Th>
                <Component.Th center>
                  <TSpan
                    font="footnote-bold"
                    colorToken="--text-card-neutral-default"
                  >
                    Discount
                  </TSpan>
                </Component.Th>
                <Component.Th />
              </Component.Tr>
            </Component.Thead>
            <Component.Tbody>
              {!loading &&
                plans.map((plan, index, plansArray) => {
                  const isStarter =
                    plan.plan_name === 'Starter' && plan.item_type === 'charge';
                  const isMonthlyInstallments =
                    toggleButtonState === EToggle.MONTHLY;

                  const domId = plan.plan_name_id;

                  const disabled = isStarter || loading || chragebeeLoading;

                  const spotlighted = !!plan.spotlight;

                  return (
                    <Component.Tr key={index}>
                      <Component.Td
                        width="210"
                        hideLeftBorder
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <IndivisualPlanDetails
                          isStarter={isStarter}
                          isInstallment={isMonthlyInstallments}
                          billingCycle={plan.billing_cycles}
                          currency={plan.currency_code}
                          planName={plan.plan_name}
                          spotlighted={spotlighted}
                          discount={plan.discount_percent}
                          price={plan.price}
                        />
                      </Component.Td>
                      <Component.Td
                        center
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <TSpan font={spotlighted ? 'body-l-bold' : 'body-m'}>
                          {plan.percent_fee}%
                        </TSpan>
                      </Component.Td>
                      <Component.Td
                        center
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <TSpan font={spotlighted ? 'body-l-bold' : 'body-m'}>
                          {!isStarter &&
                            (isNotLaptop || spotlighted ? (
                              <Trans
                                i18nKey={t(
                                  'noumena.money.subscription.noum.allowed.to.setup_renew.wrapped',
                                  {
                                    noums: plan.noum_setup,
                                  },
                                )}
                                components={{
                                  br: <br />,
                                }}
                              />
                            ) : (
                              t(
                                'noumena.money.subscription.noum.allowed.to.setup_renew',
                                {
                                  noums: plan.noum_setup,
                                },
                              )
                            ))}
                          {isStarter && plan.price && (
                            <TSpan font="body-m">
                              {convertToCurrency(
                                plan.price / 100,
                                AllCurrencyEnum.Usd,
                              )}{' '}
                              (1 Free)
                            </TSpan>
                          )}
                        </TSpan>
                      </Component.Td>
                      <Component.Td
                        center
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <TSpan font={spotlighted ? 'body-l-bold' : 'body-m'}>
                          {!isStarter &&
                            (isNotLaptop || spotlighted ? (
                              <Trans
                                i18nKey={t(
                                  'noumena.money.subscription.noum.allowed.to.setup_renew.wrapped',
                                  {
                                    noums: plan.noum_renewal,
                                  },
                                )}
                                components={{
                                  br: <br />,
                                }}
                              />
                            ) : (
                              t(
                                'noumena.money.subscription.noum.allowed.to.setup_renew',
                                {
                                  noums: plan.noum_renewal,
                                },
                              )
                            ))}
                          {isStarter && plan.renew_price && (
                            <TSpan font="body-m">
                              {convertToCurrency(
                                plan.renew_price / 100,
                                AllCurrencyEnum.Usd,
                              )}
                            </TSpan>
                          )}
                        </TSpan>
                      </Component.Td>
                      <Component.Td
                        center
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <TSpan font={spotlighted ? 'body-l-bold' : 'body-m'}>
                          {plan.discount_percent
                            ? `${plan.discount_percent}%`
                            : '-'}
                        </TSpan>
                      </Component.Td>
                      <Component.Td
                        center
                        spotlighted={spotlighted}
                        hideBottomBorder={!!plansArray[index + 1]?.spotlight}
                      >
                        <Button
                          size="small"
                          style={{ width: '109px', fontSize: '16px' }}
                          disabled={disabled}
                          primary={spotlighted}
                          secondary={!spotlighted}
                          onClick={() => onPlanPurchase(domId)}
                        >
                          <TSpan
                            font="button-m"
                            colorToken={
                              disabled
                                ? '--text-button-neutral-disabled'
                                : spotlighted
                                ? '--text-button-neutral-alt-default'
                                : '--text-button-brand-secondary-default'
                            }
                          >
                            {isStarter
                              ? `${t(
                                  'noumena.money.subscription.subscription.default.text',
                                )}`
                              : `${t(
                                  'noumena.money.subscription.subscription.getstarted.text',
                                )}`}
                          </TSpan>
                        </Button>
                      </Component.Td>
                    </Component.Tr>
                  );
                })}
            </Component.Tbody>
          </Component.Table>
        )}
      </>
    );
  },
);

export default TableView;
