import { t } from 'i18next';
import { useParams } from 'react-router';
import { Button } from '@/components/Button';
import convertToCurrency from '@/utils/currencyToCurrency';
import { TSpan } from '@/components/Typography';
import {
  useChargebeeCheckout,
  useSubscription,
  useNoumRenewalHelper,
  useNoumTransactions,
} from '@/features/money/hooks';
import {
  AllCurrencyEnum,
  type NoumTransactionDetailInputType,
  Noum_Fee_Operation_Type,
  Subscription_Plan_Type,
} from '@/apollo/generated/types';
import { Stack } from '@/layout';
import Tooltip from '@/components/Tooltip';
import { type TPlanCard } from './types';
import { PurschaseModalLaunchFronEnum, SCREEN } from '../types';
import {
  BaseSubscriptionCardCard,
  HeaderSection,
  FeatureSection,
  MobileFeature,
} from './styles';
import IndivisualPlanDetails from './IndivisualPlanDetails';

const SubscriptionCard = ({
  isStarter,
  isInstallment,
  planName = 'Starter',
  launchFrom = '',
  chamber_id,
  noum_transaction_fee_id,
  onClosePurchasePlanModal,
  transactionFee,
  discountPercent,
  noumSetup,
  noumRenewal,
  billingCycle,
  price,
  currency,
  planId,
  renewPrice,
  chamberIdAfterCreatingNoum,
  publishAndSubscribeNoum,
  launchScreen,
  itemType,
  spotlighted,
  onPlanPurchase,
}: TPlanCard) => {
  const { noumRenewalHelper } = useNoumRenewalHelper();
  const { refreshSubscriptions } = useSubscription();
  const { id } = useParams();

  const requestParameter = () => {
    let parameter: NoumTransactionDetailInputType = {};
    if (launchScreen === SCREEN.PLAN_DETAILS) {
      if (itemType === Subscription_Plan_Type.Charge) {
        parameter = { plan_type: Subscription_Plan_Type.Charge };
      } else {
        parameter = { subscription_id: Number(id) };
      }
    }

    return parameter;
  };

  const { refreshNoumData } = useNoumTransactions(requestParameter());
  const { loading } = useChargebeeCheckout({
    success: async (subscriptionOBj) => {
      onClosePurchasePlanModal();
      if (launchFrom === PurschaseModalLaunchFronEnum.NOUM_SETUP) {
        if (
          chamberIdAfterCreatingNoum &&
          subscriptionOBj.subscription_id &&
          publishAndSubscribeNoum
        ) {
          await publishAndSubscribeNoum(subscriptionOBj.subscription_id);
        }
      }
      if (launchFrom === PurschaseModalLaunchFronEnum.INITIAL_SETUP) {
        refreshSubscriptions();
      }
      if (
        launchFrom === PurschaseModalLaunchFronEnum.NOUM_RENEWAL &&
        chamber_id &&
        noum_transaction_fee_id
      ) {
        const isSuccess = await noumRenewalHelper({
          chamber_id,
          operation_type: Noum_Fee_Operation_Type.Renewal,
          subscription_id: subscriptionOBj?.subscription_id!,
          noum_transaction_fee_id,
        });

        if (isSuccess) {
          refreshSubscriptions();
          refreshNoumData();
        }
      }
    },
  });

  const disabled = isStarter || loading;

  return (
    <BaseSubscriptionCardCard spotlighted={spotlighted}>
      <HeaderSection spotlighted={spotlighted}>
        <section>
          <IndivisualPlanDetails
            spotlighted={spotlighted}
            isStarter={isStarter}
            isInstallment={isInstallment}
            billingCycle={billingCycle}
            price={price}
            currency={currency}
            discount={discountPercent}
            planName={planName}
          />
        </section>
        <section>
          <Button
            size="small"
            style={{ width: isStarter ? '78px' : '109px' }}
            disabled={disabled}
            primary={spotlighted}
            secondary={!spotlighted}
            onClick={() => onPlanPurchase(planId)}
          >
            <TSpan
              font="button-m"
              colorToken={
                disabled
                  ? '--text-button-neutral-disabled'
                  : spotlighted
                  ? '--text-button-neutral-alt-default'
                  : '--text-button-brand-secondary-default'
              }
            >
              {isStarter
                ? `${t('noumena.money.subscription.subscription.default.text')}`
                : `${t(
                    'noumena.money.subscription.subscription.getstarted.text',
                  )}`}
            </TSpan>
          </Button>
        </section>
      </HeaderSection>
      <FeatureSection>
        <MobileFeature>
          <Stack>
            <TSpan font="footnote" colorToken="--text-card-neutral-default">
              Transaction Fee
            </TSpan>
            <div style={{ marginLeft: '6px' }}>
              <Tooltip top={18} left={-80}>
                <div style={{ width: '284px' }}>
                  <TSpan
                    colorToken="--text-tooltip-neutral-alt-default"
                    font="footnote"
                  >
                    {t(
                      'noumena.money.subscription.noum.transactionfee.tooltip.text',
                    )}
                  </TSpan>
                </div>
              </Tooltip>
            </div>
          </Stack>
          <TSpan font={spotlighted ? 'body-m-bold' : 'body-m'}>
            {transactionFee}%
          </TSpan>
        </MobileFeature>
        <MobileFeature>
          <TSpan font="footnote" colorToken="--text-card-neutral-default">
            Noum Set-up Fee
          </TSpan>
          <TSpan font={spotlighted ? 'body-m-bold' : 'body-m'}>
            {isStarter && price
              ? `${convertToCurrency(
                  price / 100,
                  AllCurrencyEnum.Usd,
                )} (1 Free)`
              : t('noumena.money.subscription.noum.allowed.to.setup_renew', {
                  noums: noumSetup,
                })}
          </TSpan>
        </MobileFeature>
      </FeatureSection>
      <FeatureSection>
        <MobileFeature>
          <TSpan font="footnote" colorToken="--text-card-neutral-default">
            Noum Renewal Fee
          </TSpan>
          <TSpan font={spotlighted ? 'body-m-bold' : 'body-m'}>
            {isStarter && renewPrice
              ? convertToCurrency(renewPrice / 100 ?? 0, AllCurrencyEnum.Usd)
              : t('noumena.money.subscription.noum.allowed.to.setup_renew', {
                  noums: noumRenewal,
                })}
          </TSpan>
        </MobileFeature>
        <MobileFeature>
          <TSpan font="footnote" colorToken="--text-card-neutral-default">
            Discount
          </TSpan>
          <TSpan font={spotlighted ? 'body-m-bold' : 'body-m'}>
            {discountPercent ? `${discountPercent}%` : '-'}
          </TSpan>
        </MobileFeature>
      </FeatureSection>
    </BaseSubscriptionCardCard>
  );
};

export default SubscriptionCard;

// 1 free Noum set-up
