import Skeleton from 'react-loading-skeleton';
import { Spinner } from '@/components/Spinner';
import { useDeviceType, DeviceTypeEnum } from '@/hooks';
import { Spacer } from '@/layout';
import { BaseSubscriptionCardCard } from './styles';

const SubscriptionCardSkeleton = () => {
  const device = useDeviceType();
  const isMobile = device === DeviceTypeEnum.MOBILE;
  return (
    <>
      {isMobile && (
        <BaseSubscriptionCardCard
          spotlighted={false}
          style={{ padding: '12px' }}
        >
          <section>
            <header>
              <Skeleton width="60px" height="20px" />
            </header>
            <Skeleton width="190px" height="30px" />
            <Spacer height={24} />
            <Skeleton width="100%" height="40px" />
          </section>
          <section
            style={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              width: '100%',
              alignItems: 'center',
            }}
          >
            <div style={{ width: '90%' }}>
              <Skeleton width="90%" count={5} />
            </div>
          </section>
        </BaseSubscriptionCardCard>
      )}
      {!isMobile && (
        <div
          style={{
            position: 'relative',
            width: '100%',
            height: '400px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Spinner />
        </div>
      )}
    </>
  );
};

export default SubscriptionCardSkeleton;
