import { useState, useEffect } from 'react';
import { useGetNoumTransactionFeeDetailsQuery } from '@/apollo/graphql';
import {
  type NoumTransactionDetailInputType,
  type NoumTransactionFee,
} from '@/apollo/generated/types';
import { useError } from '@/hooks/useError';
import { useLaunchDarkly } from '@/hooks/launchDarkly';

export const useNoumTransactions = (filter: NoumTransactionDetailInputType) => {
  const [noumData, setNoumData] = useState<(NoumTransactionFee | undefined)[]>(
    [],
  );
  const { logError } = useError();
  const { flags } = useLaunchDarkly();
  const { data, loading, error, refetch } =
    useGetNoumTransactionFeeDetailsQuery({
      fetchPolicy: 'network-only',
      skip: !flags.paymentSubscriptions,
      variables: {
        noumDetailInput: filter,
      },
      onError: () => {
        logError(error, 'gqlGetNoumTransactionFeeDetails');
      },
    });

  useEffect(() => {
    setNoumData(data?.getNoumTransactionFeeDetails || []);
  }, [data]);

  useEffect(() => {
    refetch();
  }, [refetch]);

  return {
    noumData,
    loading,
    refreshNoumData: refetch,
  };
};

export default useNoumTransactions;
