import { useState, memo, useMemo, useCallback } from 'react';
import { t } from 'i18next';
import { TSpan } from '@/components/Typography';
import * as ModalComponent from '@/components/ExtendedModal';
import { useDeviceType, DeviceTypeEnum } from '@/hooks';
import { useChargebee } from '@/features/money/hooks';
import { Spacer } from '@/layout';
import { Button } from '@/components/Button';
import { Icon } from '@/components/Icon';
import * as Component from './styles';
import SubscriptionCard from './SubscriptionCard';
import { type PlanPurchaseModalProps, EToggle } from './types';
import { uiPlanOrder } from './helper';
import TableView from './TableView';
import SubscriptionCardSkeleton from './SubscriptionCardSkeleton';

const PlanPurchaseModal = memo(
  ({
    open,
    onClose,
    launchFrom,
    chamberIdAfterCreatingNoum,
    chamber_id,
    noum_transaction_fee_id,
    publishAndSubscribeNoum,
    launchScreen,
    itemType,
    closeNoumRenewModal,
  }: PlanPurchaseModalProps) => {
    const device = useDeviceType();
    const [domId, setDomId] = useState('');
    const [openConfirmModal, setOpenConfirmModal] = useState(false);
    const {
      plans: { monthly, yearly },
      loading,
    } = useChargebee();
    const isMobile = device === DeviceTypeEnum.MOBILE;
    const [toggleButtonState, setToggleButtonState] = useState<EToggle>(
      EToggle.MONTHLY,
    );

    const handleToggle = (state: EToggle) => {
      setToggleButtonState(state);
    };

    const discount = useMemo(() => {
      const discounts: number[] = [];
      monthly.forEach((plan) => discounts.push(plan.discount_percent ?? 0));
      yearly.forEach((plan) => discounts.push(plan.discount_percent ?? 0));
      return Math.max(...discounts);
    }, [monthly, yearly]);

    const plans = toggleButtonState === EToggle.MONTHLY ? monthly : yearly;

    const handleOnClose = useCallback(() => {
      onClose();
      if (closeNoumRenewModal) {
        closeNoumRenewModal();
      }
    }, [closeNoumRenewModal, onClose]);

    const handleConfirmModal = useCallback((id: string | null) => {
      if (id) {
        setOpenConfirmModal(true);
        setDomId(id);
      }
    }, []);

    const onCloseConfirmModal = () => {
      setOpenConfirmModal(false);
      const link = document.getElementById(domId);
      if (link) link.click();
    };

    return (
      <>
        <ModalComponent.Modal
          open={open}
          size={ModalComponent.ModalSize.XXL}
          enableCloseButton
          onClose={handleOnClose}
          disableBackdropClick
        >
          <ModalComponent.ModalHeader maxTitleWidth={1000} bottomPadding={10}>
            <div
              style={{
                width: '100%',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
              }}
            >
              <Spacer height={24} />
              <TSpan font="heading-xs-bold">
                {t(
                  'noumena.money.subscription.subscription.purchaseplan.heading',
                )}
              </TSpan>
              <Spacer height={24} />
              <Component.BaseToggleComponent>
                <Component.ToogleButton
                  active={toggleButtonState === EToggle.MONTHLY}
                  onClick={() => handleToggle(EToggle.MONTHLY)}
                >
                  <TSpan
                    font="body-m-bold"
                    colorToken={
                      toggleButtonState === EToggle.MONTHLY
                        ? '--text-tab-chips-brand-primary-selected'
                        : '--text-tab-basic-neutral-default'
                    }
                  >
                    {t(
                      'noumena.money.subscription.subscription.purchaseplan.tab.one.text',
                    )}
                  </TSpan>
                </Component.ToogleButton>
                <Component.ToogleButton
                  active={toggleButtonState === EToggle.YEARLY}
                  onClick={() => handleToggle(EToggle.YEARLY)}
                >
                  <TSpan
                    font="body-m"
                    colorToken={
                      toggleButtonState === EToggle.YEARLY
                        ? '--text-tab-chips-brand-primary-selected'
                        : '--text-tab-basic-neutral-default'
                    }
                  >
                    {t(
                      'noumena.money.subscription.subscription.purchaseplan.tab.two.text',
                    )}
                  </TSpan>
                  {!!discount && (
                    <Component.Discount>
                      <TSpan
                        font="footnote-bold"
                        colorToken="--text-tag-brand-primary-default"
                      >
                        {isMobile
                          ? 'Save!'
                          : t(
                              `noumena.money.subscription.subscription.purchaseplan.tab.two.discount.text`,
                              { discount },
                            )}
                      </TSpan>
                    </Component.Discount>
                  )}
                </Component.ToogleButton>
              </Component.BaseToggleComponent>
              <Spacer height={24} />
            </div>
          </ModalComponent.ModalHeader>
          <ModalComponent.ModalBody align="center" overflow="hidden">
            <Component.PlanCardsContainer>
              {!isMobile && (
                <TableView
                  loading={loading}
                  toggleButtonState={toggleButtonState}
                  plans={plans}
                  launchFrom={launchFrom}
                  itemType={itemType}
                  launchScreen={launchScreen}
                  onClosePurchasePlanModal={handleOnClose}
                  chamberIdAfterCreatingNoum={chamberIdAfterCreatingNoum}
                  publishAndSubscribeNoum={publishAndSubscribeNoum}
                  chamber_id={chamber_id}
                  noum_transaction_fee_id={noum_transaction_fee_id}
                  onPlanPurchase={handleConfirmModal}
                />
              )}

              {loading &&
                isMobile &&
                uiPlanOrder.map(() => <SubscriptionCardSkeleton />)}
              {!loading &&
                isMobile &&
                plans.map((plan) => (
                  <SubscriptionCard
                    spotlighted={!!plan.spotlight}
                    planName={plan.plan_name}
                    isStarter={plan.plan_name === 'Starter'}
                    transactionFee={plan.percent_fee}
                    discountPercent={plan.discount_percent}
                    noumSetup={plan.noum_setup}
                    noumRenewal={plan.noum_renewal}
                    billingCycle={plan.billing_cycles}
                    isInstallment={toggleButtonState === EToggle.MONTHLY}
                    currency={plan.currency_code}
                    planId={plan.plan_name_id}
                    onClosePurchasePlanModal={handleOnClose}
                    launchFrom={launchFrom}
                    price={plan.price}
                    renewPrice={plan.renew_price}
                    planValidityInMonths={plan.plan_validity_months}
                    chamber_id={chamber_id}
                    noum_transaction_fee_id={noum_transaction_fee_id}
                    chamberIdAfterCreatingNoum={chamberIdAfterCreatingNoum}
                    publishAndSubscribeNoum={publishAndSubscribeNoum}
                    launchScreen={launchScreen}
                    itemType={itemType}
                    onPlanPurchase={handleConfirmModal}
                  />
                ))}
            </Component.PlanCardsContainer>
          </ModalComponent.ModalBody>
          {!loading && (
            <ModalComponent.ModalFooter
              justifyContent="center"
              flexDirection="column"
              marginTop={isMobile ? undefined : 0}
            >
              {/* <TSpan
                font="footnote-bold"
                colorToken="--text-modal-neutral-default"
              >
                {t(
                  'noumena.money.subscription.subscription.all.plans.expire.notice',
                  { months: monthly[0]?.plan_validity_months ?? 0 },
                )}
              </TSpan> */}
              {/* <TSpan
                font="footnote"
                colorToken="--text-modal-neutral-default"
                textAlign="center"
              >
                {t(
                  'noumena.money.subscription.subscription.plan.expire.notice',
                  {
                    months: monthly[0]?.plan_validity_months ?? 0,
                  },
                )}
              </TSpan> */}
            </ModalComponent.ModalFooter>
          )}
        </ModalComponent.Modal>
        <ModalComponent.Modal
          open={openConfirmModal}
          enableCloseButton
          onClose={() => setOpenConfirmModal(false)}
        >
          <ModalComponent.ModalHeader />
          <ModalComponent.ModalBody
            align="center"
            style={{
              width: isMobile ? '300px' : '',
              paddingTop: isMobile ? '20px' : '',
            }}
          >
            <TSpan font="body-m" textAlign="center">
              Please do not edit your email in plan checkout page
            </TSpan>
          </ModalComponent.ModalBody>
          <ModalComponent.ModalFooter justifyContent="center">
            <Button
              onClick={onCloseConfirmModal}
              secondary
              rightIcon={<Icon name="chevron_right_m" size={24} />}
            />
          </ModalComponent.ModalFooter>
        </ModalComponent.Modal>
      </>
    );
  },
);

export default PlanPurchaseModal;
