import { t } from 'i18next';
import convertToCurrency from '@/utils/currencyToCurrency';
import { TSpan } from '@/components/Typography';
import { DeviceTypeEnum, useDeviceType } from '@/hooks';
import { Tag } from '@/components/Tag';
import { type AllCurrencyEnum } from '@/apollo/generated/types';
import * as Component from './styles';

type IndivisualPlanDetailsProps = {
  isStarter?: boolean;
  isInstallment?: boolean;
  billingCycle: number | null;
  price: number | null;
  currency: string | null;
  discount: number | null;
  planName: string;
  spotlighted: boolean;
};

const IndivisualPlanDetails = ({
  isStarter,
  isInstallment,
  billingCycle,
  price,
  currency,
  discount,
  planName,
  spotlighted,
}: IndivisualPlanDetailsProps) => {
  const showInstallments = !isStarter && isInstallment;
  const showCancelledCurrency =
    !isStarter && !isInstallment && !!discount && !!price && currency;
  const device = useDeviceType();
  const isMobile = device === DeviceTypeEnum.MOBILE;
  const isLongPlanName = planName.length > 10;
  const height = isLongPlanName ? 'auto' : '90px';

  return (
    <div
      style={
        !isMobile
          ? {
              padding: '12px 0px 12px 16px',
              height: `${spotlighted ? height : '70px'}`,
              boxSizing: 'border-box',
            }
          : {}
      }
    >
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          ...(isLongPlanName
            ? { flexDirection: 'column-reverse', alignItems: 'flex-start' }
            : {}),
        }}
      >
        <TSpan
          font={spotlighted && !isMobile ? 'body-xl-bold' : 'body-m-bold'}
          colorToken="--text-card-brand-primary-default"
        >
          {planName}
        </TSpan>
        {spotlighted && (
          <Tag
            size="small"
            contentFont="footnote"
            style={{
              marginLeft: isLongPlanName ? '0px' : '8px',
              marginBottom: '6px',
            }}
          >
            Popular
          </Tag>
        )}
      </div>
      <Component.BaseIndivisualPlanDetails>
        {showCancelledCurrency && (
          <Component.CancelledCurrency
            font="footnote-bold"
            colorToken="--text-card-neutral-disabled"
          >
            {convertToCurrency(
              price / 100 / (1 - discount / 100),
              currency as AllCurrencyEnum,
            )}
          </Component.CancelledCurrency>
        )}
        <Component.Currency
          font={spotlighted && !isMobile ? 'heading-s-bold' : 'body-l-bold'}
          colorToken="--text-card-neutral-highlighted"
        >
          {isStarter
            ? 'Pay as You Go'
            : convertToCurrency(price! / 100, currency! as AllCurrencyEnum)}
        </Component.Currency>
        {showInstallments && (
          <Component.Installments
            font="footnote"
            colorToken="--text-card-neutral-default"
          >
            {' '}
            Ã— {billingCycle}{' '}
            {`${t(
              'noumena.money.subscription.subscription.installments.text',
            )}`}
          </Component.Installments>
        )}
      </Component.BaseIndivisualPlanDetails>
    </div>
  );
};

export default IndivisualPlanDetails;
